import React from "react";
import dharmveer from '../img/Dharmveer.pdf'
import { Link } from "react-router-dom";
import '../Style/home.css';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faFileImage, faUserCircle } from "@fortawesome/free-regular-svg-icons";

const Home = () => {
    return (
        <>
    
            <div className="container">
                <div className="text-center ">
                    <h1 className=" font-size ">Front-end Web Developer</h1>
                    <div>
                        <p className="mt-3  text-center text-size">


                            <p>
                                <h1 className="badge m-1 p-2 ">Hello!
                                </h1>

                                My name is Dharmveer Singh. I am a Web Developer for freelancer.com.. etc, where i specialize in streamlining user experience for external clients.
                            </p>
                        </p>
                    </div>
                </div>
                <div className="text-center">
                    <a href={dharmveer} rel="noopener noreferrer " target="_blank">
                        <button className="btn  btn-info m-1">
                            <FontAwesomeIcon icon={faFileImage} /> Resume
                        </button>
                    </a>

                    <Link to="/about" ><button className="btn  btn2  m-2 btn  btn-primary active m-1 pe-4 ps-4 bounceInLeft">
                        <FontAwesomeIcon icon={faUserCircle} /> About Me
                    </button>
                    </Link>
                </div>

            </div>
        </>
    )
}
export default Home;